<script>
	import request from "@/common/request.js"
export default {
	data() { 
		return {
			statusBarHeight: 0,
			height: 0,
			windowWidth: 0,
			noticeinfo:'',
		}
	},
	async created() {
		const response = await request.noticeinfo({noticeid:1})
		this.noticeinfo = response.data
		
		console.log(response.data)
	
	},
	methods: {
		optionChange(e) {},
		goPage(name) {

