		getStatusBarHeight() {
			var self = this;
			uni.getSystemInfo({
				success: function(resp) {
					console.log("resp", resp);
					self.statusBarHeight = resp.statusBarHeight;
					self.height = (resp.safeArea.height - 44)
					self.windowWidth = resp.windowWidth
				},
			})
		},
	},
	onLoad() {
		this.getStatusBarHeight()
	}
}
</script>
